li {
    float: left;
    text-align: center;
    
}


.navbar-nav li a{
    margin-right: 20px;
    color: white;
    font: 16px "微软雅黑";
    line-height: 70px;
    text-decoration: none;
}

.navbar-nav li a:link{
	text-decoration: none;
}
.navbar-nav li a:visited{
	border-bottom: 2px solid #0fa0e9;
}
.navbar-nav li a:hover{
	text-decoration: none;
}
.navbar-nav li a:active{
	border-bottom: 2px solid #0fa0e9;
	text-decoration: none;
}

.line_ {
    width: 100%;
    height: 4px;
    margin: 20px 0;
    background: #29ABE2;
}
.white {
    color: #fff;
    word-wrap: break-word;
}